import React, { Component } from 'react';
import { StyleSheet, View, StatusBar, TouchableOpacity, Text, ScrollView, Image} from 'react-native';
const statusBarHeight = StatusBar.currentHeight;
import {
    isFirstTime,
    isRolledBack,
    packageVersion,
    currentVersion,
    checkUpdate,
    downloadUpdate,
    switchVersion,
    switchVersionLater,
    markSuccess,
    downloadAndInstallApk,
  } from 'react-native-update';
  
  import _updateConfig from '../update.json';
  const { appKey } = _updateConfig[Platform.OS];

  export default class updateCheck extends Component {
    state = {
      received: 0,
      total: 0,
    };
    componentDidMount() {
      if (isFirstTime) {
        // 必须调用此更新成功标记方法
        // 否则默认更新失败，下一次启动会自动回滚
        markSuccess();
        console.log('更新完成');
      } else if (isRolledBack) {
        console.log('刚刚更新失败了,版本被回滚.');
      }
    }
    doUpdate = async (info) => {
      try {
        const hash = await downloadUpdate(info, {
          onDownloadProgress: ({ received, total }) => {
            this.setState({
              received,
              total,
            });
          },
        });
        if (!hash) {
          return;
        }
        Alert.alert('提示', '下载完毕,是否重启应用?', [
          {
            text: '是',
            onPress: () => {
              switchVersion(hash);
            },
          },
          { text: '否' },
          {
            text: '下次启动时',
            onPress: () => {
              switchVersionLater(hash);
            },
          },
        ]);
      } catch (err) {
        Alert.alert('更新失败', err.message);
      }
    };
    checkUpdate = async () => {
      if (__DEV__) {
        // 开发模式不支持热更新，跳过检查
        return;
      }
      let info;
      try {
        info = await checkUpdate(appKey);
      } catch (err) {
        Alert.alert('更新检查失败', err.message);
        return;
      }
      if (info.expired) {
        Alert.alert('提示', '您的应用版本已更新，点击确定下载安装新版本', [
          {
            text: '确定',
            onPress: () => {
              if (info.downloadUrl) {
                // apk可直接下载安装
                if (Platform.OS === 'android' && info.downloadUrl.endsWith('.apk')) {
                  downloadAndInstallApk({
                    url: info.downloadUrl,
                    onDownloadProgress: ({ received, total }) => {
                      this.setState({
                        received,
                        total,
                      });
                    },
                  });
                } else {
                  Linking.openURL(info.downloadUrl);
                }
              }
            },
          },
        ]);
      } else if (info.upToDate) {
        Alert.alert('提示', '您的应用版本已是最新.');
      } else {
        Alert.alert('提示', '检查到新的版本' + info.name + ',是否下载?\n' + info.description, [
          {
            text: '是',
            onPress: () => {
              this.doUpdate(info);
            },
          },
          { text: '否' },
        ]);
      }
    };
    render() {
      return (
        <>
            <ScrollView>
                <StatusBar translucent={true} backgroundColor="transparent" barStyle="dark-content" />
                <View style={styles.titleBar}>
                    <Text style={styles.titleText}>检查更新</Text>
                    <Text style={styles.hitokoto}>在这里，随时获取最新版本</Text>
                    <Image source={require('../resource/images/autoUpdateStatu.png')} style={styles.autoUpdateStatu} />
                </View>
                <View style={styles.bodyBackground}>
                    <View style={styles.bodyContent}>
                        <View style={styles.manualTextGroup}>
                            <TouchableOpacity onPress={this.checkUpdate}>
                                <Image source={require('../resource/images/updateCheck.png')} style={styles.imageUpdateBtn}/>
                            </TouchableOpacity>
                        </View>
                    </View>
                    <Text style={{marginLeft:30,marginTop:30,marginBottom:20,fontSize:12,color:'#A9A9A9'}}>updateCheckServer V0.3.0</Text>
                </View>
            </ScrollView>
        </>
      );
    }
  }

const styles = StyleSheet.create({
    imageUpdateBtn: {
        width:139,
        height:110,
        
    },
    autoUpdateStatu: {
        width:183,
        height:32,
        marginLeft:30
    },
    titleBar: {
        flex: 2,
        backgroundColor: '#E9E9E9',
    },
    titleText: {
        fontSize: 40,
        fontWeight: '600',
        marginLeft: 30,
        marginTop: 110
    },
    hitokoto: {
        marginTop: 20,
        marginLeft: 30,
        marginBottom: 20,
        fontSize: 20,
        fontWeight: 'bold',
    },
    bodyBackground: {
        flex: 5,
        backgroundColor: '#E9E9E9',
    },
    bodyContent: {
        flex: 1,
        backgroundColor: '#FFFFFF',
        borderTopLeftRadius: 30,
        borderTopRightRadius: 30,
        borderBottomLeftRadius: 30,
        borderBottomRightRadius: 30,
        marginBottom: 30,
        marginTop: 20
    },
    manualTextGroup: {
        marginTop: 25,
        marginBottom: 25,
        alignItems:'center',
    },
    manualText: {
        marginTop: 5,
        fontSize: 15,
    },
});